export default {
	data() {
		return {

		};
	},
	methods:{
		goDetail(id){
			console.log(id)
			uni.navigateTo({
				url:'../news_item/news_item?id='+id
			})
		}
	},
	filters: {
		formatDate(date) {
			const nDate = new Date(date)
			const year = nDate.getFullYear()
			const month = nDate.getMonth().toString().padStart(2, 0)
			const day = nDate.getDay().toString().padStart(2, 0)
			return year + '-' + month + '-' + day
		}
	},
	props: ['newsList']
}
